import {HttpTransportType, HubConnectionBuilder, LogLevel} from "@microsoft/signalr";
import {createAuthUrl} from "./sendRequest";
import {events} from "../consts/api";
import {uuid} from "uuidv4";


export default function listenEvents(handler: (payload: any) => void) {
    
    let closed = false;
    
    const connection = new HubConnectionBuilder()
        .withUrl(createAuthUrl(`${events}?sticky=${uuid()}`, true))
        .withAutomaticReconnect()
        .configureLogging(LogLevel.Information)
        .build();

    connection.onclose(async () => {
        console.log("closed");
        if (!closed)
            await start();
    });
    
    connection.on("onEvent", payload => {
        if (closed)
            return;
        console.log(payload);
        handler(payload);
    });

    async function start() {
        if (closed)
            return;
        
        try {
            await connection.start();
            console.log("connected");
        } catch (err) {
            console.log(err);
            setTimeout(() => start(), 5000);
        }
    }

    start();

    return {
        close: function () {
            closed = true;
            connection.stop();
        }
    };
}
